	module.exports = {"data":{"markdownRemark":{"html":"<h2>Installing React</h2>\n<h3>Creating a new React project</h3>\n<p>You could just embed the React library in your webpage like so<sup>2</sup>:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>script</span> <span class=\"token attr-name\">src</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>https://cdnjs.cloudflare.com/ajax/libs/react/16.0.0/cjs/react.production.min.js<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token script language-javascript\"></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>script</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>Smart programmers want to take the more practical and productive way: <a href=\"https://github.com/facebookincubator/create-react-app\">Create React App</a></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code><span class=\"token function\">npm</span> <span class=\"token function\">install</span> -g create-react-app\ncreate-react-app my-app\n\n<span class=\"token function\">cd</span> my-app\n<span class=\"token function\">npm</span> start\n</code></pre>\n      </div>\n<p>This will set up your development environment so that you can use the latest JavaScript features, provide a nice developer experience, and optimize your app for production.</p>\n<p><code>npm start</code> will start up a development server which allows live reloading<sup>3</sup>.</p>\n<p>After you finish your project and are ready to deploy your App to production, you can just use\n<code>npm run build</code>\nto create an optimized build of your app in the <code>build</code>folder.</p>\n<h4>Usefull links</h4>\n<p><a href=\"https://github.com/facebookincubator/create-react-app#create-react-app-\">Create React App repository</a></p>\n<h4>Sources</h4>\n<p><a href=\"https://reactjs.org/docs/installation.html\">1. The React tutorial on installing</a>\n<a href=\"https://cdnjs.com/libraries/react\">2. Link to the React minimal JavaScript library on cdnjs.org</a>\n<a href=\"https://docs.npmjs.com/cli/start\">3. npm start command</a></p>","fields":{"slug":"/react/installation/"},"frontmatter":{"title":"Installation"}}},"pathContext":{"slug":"/react/installation/"}}

